.transactionHistory {
  width: 500px;
  margin: 0 auto;
  background-color: rgb(248, 248, 246);
}
.transactionHistory th {
  background-color: rgb(74, 74, 216);
  color: aliceblue;
}
th {
  height: 30px;
  font-size: 20px;
  font-weight: 700;
}
tr {
  text-align: center;
}
tr:nth-child(2n + 1) {
  background-color: rgb(232, 236, 235);
}
